import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Scheduler } from './scheduler';

@Injectable({
  providedIn: 'root'
})
export class SchedulerService {
  apiURL: string = 'http://localhost:8480/api/scheduler';

  constructor(private httpClient: HttpClient) {}

  public createScheduler(scheduler: Scheduler){}

  public updateScheduler(scheduler: Scheduler){}

  public deleteScheduler(id: number){}

  public getSchedulerById(id: number){}

  listSchedulers(request, filter) {
    const endpoint = this.apiURL + "/jobs";
    const params = request;
    return this.httpClient.get(endpoint, { params });
  }

}
